import React from 'react'
import { Avatar, Box, Card, CardContent, Paper, Typography } from '@mui/material';
import { AspectRatio } from '@mui/icons-material';
import { LoadingButton } from '@mui/lab';
interface ProfileImagenProp {
    perfil: string
}
export const ProfileImagen = ({ perfil }: ProfileImagenProp) => {
    const [loading, setLoading] = React.useState(false);
    function handleClick() {
        setLoading(!loading);
    }
    return (
        <Card style={{ width: '100%' }}>
            <CardContent>
                <Typography alignContent={"center"} >Imagen de perfil</Typography>
                <br />
                <Box
                    display="flex"
                    justifyContent="center"
                    alignItems="center"
                >
                    <Avatar alt="Cindy Baker" src={`${process.env.REACT_APP_API_COMISIONES}${perfil}`} sx={{ height: '250px', width: '250px' }} />
                </Box>
                <br />
                <div style={{ textAlign: 'center' }}>
                    <LoadingButton
                        size="small"
                        onClick={handleClick}
                        loading={loading}
                        loadingIndicator="Loading…"
                        variant="outlined"
                    >
                        <input
                            type="file"
                            hidden
                            onChange={handleClick}
                        />
                        <span>Cabiar Foto</span>
                    </LoadingButton>
                </div>
            </CardContent>
        </Card>
    )
}
